<script>
import TableComponent from "@/components/Table/Table.vue";

export default {
  name: "subAreaInQueueSignoffComponent",
  components: { TableComponent },
  data() {
    return {
      // 更換無塵室區長 facMaster
      empOption: [],
      newEmpOption: [],
      facMasterForm: {
        OldEmp_No: [],
        NewEmp_No: [],
      },
      facMasterTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "factoryCode",
          label: "廠區代號(備註)",
          width: "160",
        },
        {
          id: "mainArea",
          label: "主區域",
          width: "160",
        },
        {
          id: "floor",
          label: "FAB廠別",
          width: "125",
        },
        {
          id: "aid",
          label: "FAB ID",
          width: "100",
        },
        {
          id: "factoryDirector",
          label: "廠長(負責人)",
          width: "200",
        },
        {
          id: "modifyDate",
          label: "最近更改人日期",
        },
      ],
      facMasterSelected: [],
      facMasterTableData: [],
      facMasterTableList: [],
      facMasterPage: 1,

      // 更換非無塵室區長 notFacMaster
      notFacMasterForm: {
        OldEmp_No: [],
        NewEmp_No: [],
      },
      notFacMasterTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "factoryCode",
          label: "廠區代號(備註)",
          width: "160",
        },
        {
          id: "mainArea",
          label: "主區域",
          width: "160",
        },
        {
          id: "floor",
          label: "FAB廠別",
          width: "125",
        },
        {
          id: "aid",
          label: "FAB ID",
          width: "100",
        },
        {
          id: "factoryDirector",
          label: "廠長(負責人)",
          width: "200",
        },
        {
          id: "modifyDate",
          label: "最近更改人日期",
        },
      ],
      notEmpOption: [],
      notNewEmpOption: [],
      notFacMasterSelected: [],
      notFacMasterTableData: [],
      notFacMasterTableList: [],
      notFacMasterRoomPage: 1,

      // 更換次區域簽核者
      secondAreaSignOffForm: {
        OldEmp_No: [],
        NewEmp_No: [],
      },
      secondAreaSignOffOption: [],
      subAreaSignOffTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "factoryInfo",
          label: "廠區代號(備註)",
          width: "160",
        },
        {
          id: "mainArea",
          label: "主區域(ID)",
          width: "140",
        },
        {
          id: "subArea",
          label: "次區域(ID)",
          width: "140",
        },
        {
          id: "typeInfo",
          label: "Type",
          width: "140",
        },
        {
          id: "empInfo",
          label: "廠長(負責人)",
          width: "300",
        },
        {
          id: "modifyInfo",
          label: "最近更改人日期",
          width: "175",
        },
      ],
      newSecondAreaSignOff: [],
      secondAreaSignOffSelected: [],
      secondAreaSignOffTableData: [],
      secondAreaSignOffTableList: [],
      secondAreaSignOffPage: 1,

      // 更換當站簽核者
      processorForm: {
        OldEmp_No: [],
        NewEmp_No: [],
      },
      processorOption: [],
      processorTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "proc_Name",
          label: "流程名稱",
          width: "150",
        },
        {
          id: "empInfo",
          label: "簽核人員",
          width: "180",
        },
        {
          id: "create_User",
          label: "申請人工號",
          width: "150",
        },
        {
          id: "rr_Id",
          label: "申請單號",
          width: "150",
        },
        {
          id: "act_desc",
          label: "簽核點",
          width: "150",
        },
        {
          id: "create_Date",
          label: "申請日期",
        },
      ],
      processorSelected: [],
      newProcessor: [],
      processorTableData: [],
      processorTableList: [],
      processorPage: 1,
    };
  },
  computed: {},
  methods: {
    // 更換無塵室區長 facMaster
    async facMasterSearchHandler(evt) {
      try {
        const keyCode = evt.code;
        const value = evt.target.value;
        if (keyCode === "Enter" || keyCode === "NumpadEnter") {
          const res = await this.$store.dispatch("getWorkerList", value);
          this.empOption = res.data.data;
        }
      } catch (err) {
        console.log(err);
      }
    },
    facMasterClearSearch(e) {
      if (!e) {
        this.empOption = [];
      }
    },
    async searchFacMaster() {
      try {
        const obj = {
          emp_no: this.facMasterForm.OldEmp_No,
          location_type: "1",
        };
        const params = this.getURLNewParams(obj);
        const res = await this.$store.dispatch("OSHQryFacMaster", params);
        let data = res.data.data;
        this.facMasterTableData = JSON.parse(JSON.stringify(data));
        this.facMasterTableData = this.facMasterListColumns(
          this.facMasterTableData
        );
        this.facMasterTableList = this.facMasterTableData.slice(0, 10);
        this.facMasterPage = 1;
        this.$refs.facMasterTable.$refs.multipleTable.clearSelection();
      } catch (err) {
        console.log(err);
      }
    },
    async newEmpFacMasterSearchHandler(evt) {
      try {
        const keyCode = evt.code;
        const value = evt.target.value;
        if (keyCode === "Enter" || keyCode === "NumpadEnter") {
          const res = await this.$store.dispatch("getWorkerList", value);
          this.newEmpOption = res.data.data;
        }
      } catch (err) {
        console.log(err);
      }
    },
    newEmpFacMasterClearSearch(e) {
      if (!e) {
        this.newEmpOption = [];
      }
    },
    facMasterSelection(item) {
      this.facMasterSelected = item;
    },
    async updFacMaster() {
      try {
        const aids = this.facMasterSelected.map((e) => e.aid);
        const obj = {
          NewEmp_No: this.facMasterForm.NewEmp_No,
          AIds: aids,
          OldEmp_No: this.facMasterForm.OldEmp_No,
          Location_Type: "1",
        };
        const res = await this.$store.dispatch("OSHUpdFacMaster", obj);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "更換無塵室區長成功",
          });
          this.searchFacMaster();
          this.$refs.facMasterTable.$refs.multipleTable.clearSelection();
          this.facMasterPage = 1;
        }
      } catch (err) {
        console.log(err);
      }
    },
    facMasterChangePage(page) {
      this.facMasterPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.facMasterTableList = this.facMasterTableData.slice(cur, last);
    },
    facMasterListColumns(formData) {
      formData.forEach((e, index) => {
        e.index = index + 1;
        e.factoryCode = `${e.ld2}(${e.ld2E})`;
        e.mainArea = `${e.ld4}(${e.ld4})`;
        e.factoryDirector = `${e.emp_Name}(${e.sponsor},${e.emp_Email})(${e.emp_Dept_Full_Name}, ext=${e.emp_Extension_No})`;
        e.modifyDate = `${e.modify_User}(${e.modify_Date})`;
      });
      return formData;
    },

    // 更換非無塵室區長 notFacMaster
    async notFacMasterSearchHandler(evt) {
      try {
        const keyCode = evt.code;
        const value = evt.target.value;
        if (keyCode === "Enter" || keyCode === "NumpadEnter") {
          const res = await this.$store.dispatch("getWorkerList", value);
          this.notEmpOption = res.data.data;
        }
      } catch (err) {
        console.log(err);
      }
    },
    notFacMasterClearSearch(e) {
      if (!e) {
        this.notEmpOption = [];
      }
    },
    async searchNotFacMaster() {
      try {
        const obj = {
          emp_no: this.notFacMasterForm.OldEmp_No,
          location_type: "0",
        };
        const params = this.getURLNewParams(obj);
        const res = await this.$store.dispatch("OSHQryFacMaster", params);
        let data = res.data.data;
        this.notFacMasterTableData = JSON.parse(JSON.stringify(data));
        this.notFacMasterTableData = this.notFacMasterListColumns(
          this.notFacMasterTableData
        );
        this.notFacMasterTableList = this.notFacMasterTableData.slice(0, 10);
        this.notFacMasterRoomPage = 1;
        this.$refs.notFacMasterTable.$refs.multipleTable.clearSelection();
      } catch (err) {
        console.log(err);
      }
    },
    async newEmpNotFacMasterSearchHandler(evt) {
      try {
        const keyCode = evt.code;
        const value = evt.target.value;
        if (keyCode === "Enter" || keyCode === "NumpadEnter") {
          const res = await this.$store.dispatch("getWorkerList", value);
          this.notNewEmpOption = res.data.data;
        }
      } catch (err) {
        console.log(err);
      }
    },
    newEmpNotFacMasterClearSearch(e) {
      if (!e) {
        this.notNewEmpOption = [];
      }
    },
    notFacMasterSelection(item) {
      this.notFacMasterSelected = item;
    },
    async updNotFacMaster() {
      try {
        const aids = this.notFacMasterSelected.map((e) => e.aid);
        const obj = {
          NewEmp_No: this.notFacMasterForm.NewEmp_No,
          AIds: aids,
          OldEmp_No: this.notFacMasterForm.OldEmp_No,
          Location_Type: "0",
        };
        const res = await this.$store.dispatch("OSHUpdFacMaster", obj);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "更換非無塵室區長成功",
          });
          this.searchNotFacMaster();
          this.$refs.notFacMasterTable.$refs.multipleTable.clearSelection();
          this.notFacMasterRoomPage = 1;
        }
      } catch (err) {
        console.log(err);
      }
    },
    notFacMasterRoomChangePage(page) {
      this.notFacMasterRoomPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.notFacMasterTableList = this.notFacMasterTableData.slice(cur, last);
    },
    notFacMasterListColumns(formData) {
      formData.forEach((e, index) => {
        e.index = index + 1;
        e.factoryCode = `${e.ld2}(${e.ld2E})`;
        e.mainArea = `${e.ld4}(${e.ld4})`;
        e.factoryDirector = `${e.emp_Name}(${e.sponsor},${e.emp_Email})(${e.emp_Dept_Full_Name}, ext=${e.emp_Extension_No})`;
        e.modifyDate = `${e.modify_User}(${e.modify_Date})`;
      });
      return formData;
    },

    // 更換次區域簽核者
    async secondAreaSignOffSearchHandler(evt) {
      try {
        const keyCode = evt.code;
        const value = evt.target.value;
        if (keyCode === "Enter" || keyCode === "NumpadEnter") {
          const res = await this.$store.dispatch("getWorkerList", value);
          this.secondAreaSignOffOption = res.data.data;
        }
      } catch (err) {
        console.log(err);
      }
    },
    secondAreaSignOffClearSearch(e) {
      if (!e) {
        this.secondAreaSignOffOption = [];
      }
    },
    async searchSecondAreaSignOff() {
      try {
        const obj = {
          emp_no: this.secondAreaSignOffForm.OldEmp_No,
        };
        const params = this.getURLNewParams(obj);
        const res = await this.$store.dispatch(
          "OSHQrySecondAreaSignOff",
          params
        );
        let data = res.data.data;
        this.secondAreaSignOffTableData = JSON.parse(JSON.stringify(data));
        this.secondAreaSignOffTableData = this.secondAreaSignOffListColumns(
          this.secondAreaSignOffTableData
        );
        this.secondAreaSignOffTableList = this.secondAreaSignOffTableData.slice(
          0,
          10
        );
        this.secondAreaSignOffPage = 1;
        this.$refs.subAreaSignerTable.$refs.multipleTable.clearSelection();
      } catch (err) {
        console.log(err);
      }
    },
    subAreaSignOffSelection(item) {
      this.secondAreaSignOffSelected = item;
    },
    async newEmpSecondAreaSignOffSearchHandler(evt) {
      try {
        const keyCode = evt.code;
        const value = evt.target.value;
        if (keyCode === "Enter" || keyCode === "NumpadEnter") {
          const res = await this.$store.dispatch("getWorkerList", value);
          this.newSecondAreaSignOff = res.data.data;
        }
      } catch (err) {
        console.log(err);
      }
    },
    newEmpSecondAreaSignOffClearSearch(e) {
      if (!e) {
        this.newSecondAreaSignOff = [];
      }
    },
    async updSecondAreaSignOff() {
      try {
        const ids = this.secondAreaSignOffSelected.map((e) => e.id);
        const obj = {
          NewEmp_No: this.secondAreaSignOffForm.NewEmp_No,
          Ids: ids,
          OldEmp_No: this.secondAreaSignOffForm.OldEmp_No,
        };
        const res = await this.$store.dispatch("OSHUpdSecondAreaSignOff", obj);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "更換次區域簽核者成功",
          });
          this.searchSecondAreaSignOff();
          this.$refs.subAreaSignerTable.$refs.multipleTable.clearSelection();
          this.secondAreaSignOffPage = 1;
        }
      } catch (err) {
        console.log(err);
      }
    },
    secondAreaSignOffChangePage(page) {
      this.secondAreaSignOffPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.secondAreaSignOffTableList = this.secondAreaSignOffTableData.slice(
        cur,
        last
      );
    },
    secondAreaSignOffListColumns(formData) {
      formData.forEach((e, index) => {
        e.index = index + 1;
        e.factoryInfo = `${e.ld2}(${e.ld1})`;
        e.mainArea = `${e.ld3}(${e.floor_Id})`;
        e.subArea = `${e.ld4}(${e.area_Id})`;
        e.typeInfo = `${e.member_Type}/${e.area_Type_Name}`;
        e.empInfo = `${e.emp_Name}(${e.emp_No}, ${e.e_Mail})(${e.dept_Name}, ext=${e.ext_No})`;
        e.modifyInfo = `${e.modify_User}(${e.modify_Date})`;
      });
      return formData;
    },

    // 更換當站簽核者
    async processorSearchHandler(evt) {
      try {
        const keyCode = evt.code;
        const value = evt.target.value;
        if (keyCode === "Enter" || keyCode === "NumpadEnter") {
          const res = await this.$store.dispatch("getWorkerList", value);
          this.processorOption = res.data.data;
        }
      } catch (err) {
        console.log(err);
      }
    },
    processorClearSearch(e) {
      if (!e) {
        this.processorOption = [];
      }
    },
    async searchProcessor() {
      try {
        const obj = {
          emp_no: this.processorForm.OldEmp_No,
        };
        const params = this.getURLNewParams(obj);
        const res = await this.$store.dispatch("OSHQryProcessor", params);
        const data = res.data.data;
        this.processorTableData = JSON.parse(JSON.stringify(data));
        this.processorTableData = this.processorListColumns(
          this.processorTableData
        );
        this.processorTableList = this.processorTableData.slice(0, 10);
        this.processorPage = 1;
        this.$refs.processorTable.$refs.multipleTable.clearSelection();
      } catch (err) {
        console.log(err);
      }
    },
    processorSelection(item) {
      this.processorSelected = item;
    },
    async newEmpProcessorSearchHandler(evt) {
      try {
        const keyCode = evt.code;
        const value = evt.target.value;
        if (keyCode === "Enter" || keyCode === "NumpadEnter") {
          const res = await this.$store.dispatch("getWorkerList", value);
          this.newProcessor = res.data.data;
        }
      } catch (err) {
        console.log(err);
      }
    },
    newEmpProcessorClearSearch(e) {
      if (!e) {
        this.newProcessor = [];
      }
    },
    async updProcessor() {
      try {
        const rrIds = this.processorSelected.map((e) => e.rr_Id);
        const obj = {
          NewEmp_No: this.processorForm.NewEmp_No,
          rr_id: rrIds,
          OldEmp_No: this.processorForm.OldEmp_No,
        };
        const res = await this.$store.dispatch("OSHUpdProcessor", obj);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "更換當站簽核者成功",
          });
          this.searchProcessor();
          this.$refs.processorTable.$refs.multipleTable.clearSelection();
          this.processorPage = 1;
        }
      } catch (err) {
        console.log(err);
      }
    },
    processorChangePage(page) {
      this.processorPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.processorTableList = this.processorTableData.slice(cur, last);
    },
    processorListColumns(formData) {
      formData.forEach((e, index) => {
        e.index = index + 1;
        e.empInfo = `${e.processor_Empno}(${e.name})`;
      });
      return formData;
    },
  },
  mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./subAreaInQueueSignoff.html"></template>
